/* ****************************************************************************
 * ADMIN-LTE OVERRIDE
 */
/* ****************************************************************************
 * ADAMA-WEB SPECIFIC
 */
[ng-message] {
  background: #fcf8e3;
  margin-bottom: 5px;
  padding: 5px 10px;
}
.famfamfam-flags {
  display: inline-block;
}
.navbar-nav > .user-menu > .dropdown-menu > li.user-header {
  height: auto;
}
.angular-table-pager {
  margin: 20px 0;
}
.angular-table-pager .pagination {
  margin: 0;
}
.angular-table-pager .pagination a {
  cursor: pointer;
  height: 2.5em;
}
.angular-table-pager > div + div {
  text-align: right;
}
.crud-search-box input + span .btn {
  -webkit-transition: border-color 0.15s ease-in-out 0s;
  transition: border-color 0.15s ease-in-out 0s;
}
.crud-search-box input:focus + span .btn {
  border-bottom: 1px solid #3c8dbc;
  border-right: 1px solid #3c8dbc;
  border-top: 1px solid #3c8dbc;
}
@media (max-width: 767px) {
  .crud-content-header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
  }
  .crud-content-header form {
    margin-right: 0;
  }
  .crud-content-header crud-search-field {
    -webkit-box-ordinal-group: 2;
    -webkit-order: 1;
        -ms-flex-order: 1;
            order: 1;
    width: 100%;
  }
  .crud-content-header btn-create {
    margin-bottom: 10px;
  }
  .crud-content-header h1 {
    -webkit-box-ordinal-group: 0;
    -webkit-order: -1;
        -ms-flex-order: -1;
            order: -1;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-bottom: 10px;
  }
}
btn-create + crud-search-field form {
  margin-right: 10px;
}
.file-upload--zone {
  background: rgba(120, 120, 120, 0.5);
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
  height: 100px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  text-align: center;
}
.file-upload--zone input {
  display: none;
}
.file-upload--zone .only-dragover {
  display: none;
}
.file-upload--zone.dragover {
  background: rgba(0, 120, 0, 0.5);
}
.file-upload--zone.dragover span {
  display: none;
}
.file-upload--zone.dragover .only-dragover {
  display: block;
}
.file-upload--dropzone {
  background: rgba(255, 120, 0, 0.5);
}
#loading-bar .bar,
#loading-bar .peg {
  height: 5px;
}
#loading-bar .bar {
  background: #222d32;
}
.modal ~ #loading-bar .bar {
  background: #ecf0f5;
}
